import { ref, onMounted, onBeforeUnmount } from "vue";
import screenfull from "screenfull";

const useFullscreen = ({ screenTrigger } = {}) => {
  const isFullscreen = ref(false);
  onMounted(() => {
    if (screenfull.isEnabled) screenfull.onchange(escapeKeyPressListner);
    isFullscreen.value = screenfull.isFullscreen;
  });

  onBeforeUnmount(() => {
    screenfull.off("change", escapeKeyPressListner);
  });

  function escapeKeyPressListner() {
    if (!screenfull.isFullscreen) {
      isFullscreen.value = false;
      if (screenTrigger) screenTrigger();
    }
  }
  function closeFullscreen() {
    isFullscreen.value = false;
    screenfull.exit();
  }
  async function toggleFullscreen(target) {
    if (screenfull.isEnabled) {
      if (!screenfull.isFullscreen) {
        isFullscreen.value = true;
        await screenfull.request(target || document.body);
      } else {
        isFullscreen.value = false;
        await screenfull.exit();
      }
      if (screenTrigger) screenTrigger();
    }
  }

  return { isFullscreen, toggleFullscreen, closeFullscreen };
};

export default useFullscreen;
